import Chart from "./Chart.jsx";

const ExpensesChart=(props)=>{

    const dataPoints=[
        {
            label: "Jan",
            value: 0
        },
        {
            label: "Feb",
            value: 0
        },
        {
            label: "Mar",
            value: 0
        },
        {
            label: "Apr",
            value: 0
        },
        {
            label: "May",
            value: 0
        },
        {
            label: "Jun",
            value: 0
        },
        {
            label: "Jul",
            value: 0
        },
        {
            label: "Aug",
            value: 0
        },
        {
            label: "Sep",
            value: 0
        },
        {
            label: "Oct",
            value: 0
        },
        {
            label: "Nov",
            value: 0
        },
        {
            label: "Dec",
            value: 0
        },
    ];

    for(let expense of props.expenses){
        let expensemonth=expense.date.getMonth();
        dataPoints[expensemonth].value+=+expense.price;
    }

    return (
        <Chart dataPoints={dataPoints}/>
    );
}

export default ExpensesChart;